import React, { useState } from 'react';
import { Link, BrowserRouter, Route, Switch } from 'react-router-dom'
import {
  Collapse,
  Navbar,
  NavbarToggler,
  Nav
} from 'reactstrap';
import Home from '../pages/Home'
import App from '../App'
import Aboutus from '../pages/Aboutus'
import Principalmassage from '../pages/Principalmassage'
import Directormessage from '../pages/Directormessage'
import Managementmessage from '../pages/Managementmessage'
import Admissionprocedure from '../pages/Admissionprocedure'
import Ourteam from '../pages/Ourteam'
import Facilities from '../pages/Facilities'

import Gallery from '../pages/Gallery'

const Navigation = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);


  const Drop = () => {
    document.getElementById("dropdown").style.display = "block";

  }
  const Dropup = () => {
    document.getElementById("dropdown").style.display = "none";

  }

  const Drop1 = () => {
    document.getElementById("dropdown1").style.display = "block";

  }
  const Dropup1 = () => {
    document.getElementById("dropdown1").style.display = "none";

  }



  //sticky nav
  // const navbar = document.getElementById("navbar");
  // const sticky = navbar.offsetTop;

  //     window.onscroll = () =>{myfunction()};

  //     const myfunction = () =>{
  //       if (window.pageYOffset >= sticky) {
  //         navbar.classList.add("sticky")
  //       } else {
  //         navbar.classList.remove("sticky");
  //       } 
  //       }

  return (
    <BrowserRouter>
      <div id='navbar' className=''>
        <div className='mx-auto justify-center flex'>
          <img src={`${process.env.PUBLIC_URL}/favicon.ico`} className='hidden fixed md:block mt-5 z-30' alt='logo' />
        </div>
        <div className='fixed w-full z-20'>


          <div className='bg-pink-600 hidden md:flex py-1 px-6 flex'>
            <div className='w-1/3'>
              <a href="mailto:School@gmail.com" className='text-xs font-semibold '>School@gmail.com</a>
            </div>
            <div className='w-1/3'>
              <div className='mx-auto flex justify-center'>
                <a href="mailto:School@gmail.com" className='text-xs font-semibold'>School@gmail.com</a>
              </div>
            </div>
            <div className='w-1/3'>
              <div className='ml-auto flex'>
                <a href="tel:9074993859" className='text-xs font-semibold ml-auto flex'>9074993859</a>
              </div>

            </div>
          </div>


          <Navbar className='bg-indigo-400  w-full' expand="md">
            <div className='flex '>
              <Link to="/" className='text-black my-auto flex items-center font-semibold font-mono text-xl hover:no-underline hover:text-white '>School Name
              <img src={`${process.env.PUBLIC_URL}/favicon.ico`} className='block md:hidden ml-2 h-8 w-8' alt='logo' />
              </Link>
            </div>
            <NavbarToggler onClick={toggle} className='text-black border-black border-2 rounded-full' style={{ backgroundImage: `url(${require("../image/navtoggle.svg")})`, backgroundRepeat: 'no-repeat', backgroundPosition: 'center' }} />
            <Collapse isOpen={isOpen} navbar className=''>
              <Nav className="ml-auto" navbar>



                <Link to='/' className='mx-2 font-serif'>Home</Link>


                <div className='mx-2 font-serif cursor-pointer' onMouseEnter={Drop} onMouseLeave={Dropup}>About Us
                  <ul className='hidden absolute mt-0 z-10' id='dropdown'>
                    <li><Link to='/Aboutus' className='mx-2 font-serif bg-indigo-700 hover:bg-indigo-400 py-2 px-4 items-center flex'>About Us</Link></li>
                    <li><Link to='/Principalmassage' className='mx-2 font-serif bg-indigo-700 hover:bg-indigo-400 py-2 px-4 items-center flex'>Principal's Massage</Link></li>
                    <li><Link to='/Directormessage' className='mx-2 font-serif bg-indigo-700 hover:bg-indigo-400 py-2 px-4 items-center flex'>Director's Massage</Link></li>
                    <li><Link to='/Managementmessage' className='mx-2 font-serif bg-indigo-700 hover:bg-indigo-400 py-2 px-4 items-center flex'>Management Message</Link></li>
                  </ul>
                </div>
                <div className='mx-2 font-serif cursor-pointer' onMouseEnter={Drop1} onMouseLeave={Dropup1}>Academics
                  <ul className='hidden absolute mt-0 z-10' id='dropdown1'>
                    <li><Link to='/Admissionprocedure' className='mx-2 font-serif bg-indigo-700 hover:bg-indigo-400 py-2 px-4 items-center flex'>Admission Procedure</Link></li>
                    <li><Link to='/Ourteam' className='mx-2 font-serif bg-indigo-700 hover:bg-indigo-400 py-2 px-4 items-center flex'>Our Team</Link></li>
                    <li><Link to='/ATLLab' className='mx-2 font-serif bg-indigo-700 hover:bg-indigo-400 py-2 px-4 items-center flex'>ATL Lab</Link></li>
                  </ul>
                </div>
                <Link to='/Facilities' className='mx-2 font-serif'>Facilities</Link>
                <Link to='/Gallery' className='mx-2 font-serif'>Gallery</Link>
                <Link to='/Contactus' className='mx-2 font-serif'>Contact Us</Link>

                <Link to='/Home' className='mx-4 bg-red-600 rounded-lg py-1 px-2 border-black cursor-pointer font-serif'>Login</Link>

              </Nav>
            </Collapse>
          </Navbar>

          <Switch>


          </Switch>

        </div>
      </div>
      <Route exact path="/" component={Home} />
      <Route exact path="/Aboutus" component={Aboutus} />
      <Route exact path="/Principalmassage" component={Principalmassage} />
      <Route exact path="/Directormessage" component={Directormessage} />
      <Route exact path="/Managementmessage" component={Managementmessage} />
      <Route exact path="/Admissionprocedure" component={Admissionprocedure} />
      <Route exact path="/Ourteam" component={Ourteam} />
      <Route exact path="/Facilities" component={Facilities} />
      <Route exact path="/Gallery" component={Gallery} />
    </BrowserRouter>
  );
}

export default Navigation;